.article {
    padding-bottom: 10.8rem;
    @media (max-width: 1200px) {
        padding-bottom: 7.6rem;
    }
    @media(max-width: 768px) {
        padding-bottom: 6rem;
    }
    &-content {
        margin-top: 48px;
        font-size: 1.8rem;
        line-height: 1.55;
        color: $white;
        @media (max-width: 1200px) {
            margin-top: 40px;
        }
        @media (max-width: 992px) {
            margin-top: 30px;
        }
        @media (max-width: 768px) {
            margin-top: 20px;
        }
        h1 {
            position: relative;
            font-size: 3.2rem;
            line-height: 1.5;
            font-weight: 700;
            text-transform: uppercase;
            color: $white;
            padding-bottom: 18px;
            margin-bottom: 7.6rem;
            @media(max-width: 768px) {
                margin-bottom: 3rem;
                padding-bottom: 0;
                font-size: 20px;
                text-align: center;
            }
            &:last-child {
                margin-bottom: 0;
            }

            &::before {
                content: "";
                display: block;
                position: absolute;
                bottom: 0;
                left: 0;
                width: 176px;
                height: 4px;
                background: $orange-dim;
                margin-right: 0;
                @media(max-width: 768px) {
                    content: none;
                }
            }
        }

        h2, h3, h4, h5, h6 {
            font-size: 2.4rem;
            line-height: 1.5;
            font-weight: 700;
            color: $orange-dim;
            text-transform: uppercase;
            margin: 10.8rem 0 2.4rem;
            @media (max-width: 1200px) {
                margin-top: 7.6rem;
            }
            @media(max-width: 992px) {
                margin-top: 6rem;
            }
            @media (max-width: 768px) {
                margin-top: 4rem;
                text-align: center;
                font-size: 16px;
            }
            &:first-child {
                margin-top: 0;
            }

            &:last-child {
                margin-bottom: 0;
            }

            &:first-of-type {
                margin-top: 7.6rem;
                @media(max-width: 992px) {
                    margin-top: 6rem;
                }
                @media (max-width: 768px) {
                    margin-top: 4rem;
                }
            }
        }

        p {
            margin-top: 2.4rem;

            &:first-child {
                margin-top: 0;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }

        a {
            @include transition();
            color: $orange-dim;
            text-decoration: underline;

            &:hover {
                color: $link-color--hover;
            }

            &:active {
                color: $link-color--active;
            }
        }

        blockquote {
            position: relative;
            font-size: 2rem;
            line-height: 1.4;
            font-style: italic;
            padding-left: 50px;
            margin: 6.4rem 0 10.8rem;
            @media (max-width: 1200px) {
                margin: 5.4rem 0 7.8rem;
            }
            @media (max-width: 768px) {
                margin: 4rem 0;
                padding-left: 20px;
            }

            &:first-child {
                margin-top: 0;
            }

            &:last-child {
                margin-bottom: 0;
            }

            &::before {
                content: "";
                display: block;
                position: absolute;
                top: 0;
                left: 30px;
                width: 2px;
                height: 100%;
                background: $orange-dim;
                @media(max-width: 768px) {
                    left: 0;
                }
            }

            p {
                margin-top: 16px;

                &:first-child {
                    margin-top: 0;
                }
            }
        }

        ul {
            list-style: none;
            margin: 3.2rem 0 6.4rem;
            font-weight: 600;
            @media (max-width: 768px) {
                margin-bottom: 4rem;
            }
            &:first-child {
                margin-top: 0;
            }

            &:last-child {
                margin-bottom: 0;
            }

            li {
                position: relative;
                padding-left: 32px;
                margin-top: 3.2rem;
                @media (max-width: 768px) {
                    margin-top: 2rem;
                }
                &:first-child {
                    margin-top: 0;
                }

                &::before {
                    content: "";
                    display: block;
                    position: absolute;
                    top: 13px;
                    left: 0;
                    width: 16px;
                    height: 2px;
                    background: $orange-dim;
                }
            }
        }

        ol {
            list-style: none;
            counter-reset: ol;
            font-weight: 600;
            margin: 3.2rem 0 6.4rem;
            @media (max-width: 768px) {
                margin-bottom: 4rem;
            }
            &:first-child {
                margin-top: 0;
            }

            &:last-child {
                margin-bottom: 0;
            }

            li {
                counter-increment: ol;
                margin-top: 3.2rem;
                @media (max-width: 768px) {
                    margin-top: 2rem;
                }
                &:first-child {
                    margin-top: 0;
                }

                &::before {
                    content: counter(ol) ".";
                    margin-right: 8px;
                    font-weight: 700;
                    color: $orange-dim;
                }
            }
        }

        table {
            box-sizing: border-box;
            width: 100%;
            height: 100%;
            border-collapse: collapse;
            border-spacing: 0;
            font-size: 1.8rem;
            line-height: 2.8rem;
            color: $white;
            background-color: rgba(51, 51, 51, 0.6);
            border: 1px solid rgba(243, 243, 243, 0.6);
            tr {
                border-bottom: 1px solid rgba(243, 243, 243, 0.6);
                &:last-child {
                    border-bottom: 0;
                }
            }
            th {
                height: 10rem;
                padding: 22px 72px;
                font-weight: $font-weight--700;
                vertical-align: middle;
                text-align: left;
                white-space: nowrap;
                @media(max-width: 1200px) {
                    padding: 15px 50px;
                }
                @media(max-width: 992px) {
                    padding: 15px 40px;
                }
                @media(max-width: 768px) {
                    padding: 10px 20px;
                }
                &:first-child {
                    min-width: 500px;
                    padding: 22px 86px;
                    @media(max-width: 1200px) {
                        min-width: 450px;
                        padding: 15px 60px;
                    }
                    @media(max-width: 992px) {
                        min-width: 400px;
                        padding: 15px 50px;
                    }
                    @media(max-width: 768px) {
                        min-width: 250px;
                        padding: 10px 20px;
                    }
                }
            }
            td {
                height: 10rem;
                padding: 22px 72px;
                white-space: nowrap;
                vertical-align: middle;
                @media(max-width: 1200px) {
                    padding: 15px 50px;
                }
                @media(max-width: 992px) {
                    padding: 15px 40px;
                }
                @media(max-width: 768px) {
                    padding: 10px 20px;
                }
                &:first-child {
                    min-width: 500px;
                    padding: 22px 86px;
                    white-space: unset;
                    @media(max-width: 1200px) {
                        min-width: 450px;
                        padding: 15px 60px;
                    }
                    @media(max-width: 992px) {
                        min-width: 400px;
                        padding: 15px 50px;
                    }
                    @media(max-width: 768px) {
                        min-width: 250px;
                        padding: 10px 20px;
                    }
                }
                &.value {
                    font-weight: $font-weight--700;
                    color: $orange-dim;
                }
            }
        }
        .table {
            display: flex;
            overflow-x: auto;
            margin-bottom: 4rem;
        }
    }

    &-slider {
        max-width: 1264px;
        margin: 10.8rem auto 0;
        padding: 0 64px 26px;
        @media (max-width: 1200px) {
            margin-top: 7.6rem;
        }
        @media(max-width: 768px) {
            margin: 6rem auto 0;
            padding: 0;
            padding-bottom: 26px;
        }
        &.off-controls {
            .article-slider__btn--prev, .article-slider__btn--next, .swiper-pagination {
                display: none !important;
                padding-bottom: 0;
            }
        }

        .swiper-slide {
            height: auto;
        }

        & > .swiper-pagination-bullets {
            display: flex;
            align-items: center;
            justify-content: center;
            bottom: 0;
            left: 0;
            width: 100%;

            .swiper-pagination-bullet {
                width: 8px;
                height: 8px;
                opacity: 1;
                background: $white-opacity-8;
                margin: 0 9px;

                &-active {
                    background: $orange-dim;
                }
            }
        }

        &__btn {
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            z-index: 1;
            top: 50%;
            width: 48px;
            height: 48px;
            background: $orange-dim;
            border: none;
            transform: translateY(-50%);
            cursor: pointer;
            @media(max-width: 768px) {
                width: 35px;
                height: 35px;
            }
            .icon {
                display: flex;
                width: 16px;
                @media(max-width: 768px) {
                    width: 13px;
                }
                svg {
                    width: 100%;
                }
            }

            path {
                @include transition();
            }

            &--prev {
                right: auto;
                left: 0;

                .icon {
                    transform: rotate(180deg);
                }
            }

            &--next {
                right: 0;
                left: auto;
            }

            &.swiper-button-disabled {
                background: $gray;
                cursor: unset;
                path {
                    fill: $white;
                }
            }
        }
    }

    &-slide {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        width: 100%;
        height: 100%;

        &__title {
            font-size: 2.4rem;
            line-height: 1.5;
            font-weight: 700;
            color: $orange-dim;
            text-align: center;
            text-transform: uppercase;
            margin-bottom: 30px;
        }

        &__img {
            position: relative;
            width: 100%;
            height: 0;
            padding-bottom: 56.25%;

            img {
                position: absolute;
                top: 50%;
                left: 50%;
                max-width: 100%;
                max-height: 100%;
                transform: translate(-50%, -50%);
            }
        }

        &__inner {
            position: relative;
            width: 100%;
            height: 0;
            padding-bottom: 56.25%;
            background-color: transparent;

            .video-btn {
                display: flex;
                align-items: center;
                justify-content: center;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: transparent;
                border: none;
                &::before {
                    content: "";
                    display: block;
                    position: absolute;
                    z-index: 1;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background: $project-overlay-base;
                }

                &::after {
                    @include transition();
                    @include containBg();
                    content: "";
                    display: block;
                    z-index: 1;
                    width: 80px;
                    height: 80px;
                    background-image: url(../images/video-play.svg);
                }

                &:hover {
                    &::after {
                        transform: scale(1.05);
                    }
                }

                img {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    max-width: 100%;
                    max-height: 100%;
                    -webkit-transform: translate(-50%, -50%);
                    transform: translate(-50%, -50%);
                }
            }

            iframe {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }
        }
    }
}